.hcharts rect.highcharts-background{
    fill: transparent;
}

html:not(.honeywell-dark-theme) .hcharts text {
    fill: black;
    font-weight: 700;
    font-size: 10px !important;
}

html.honeywell-dark-theme .hcharts text {
    fill: white !important;
    font-weight: 700;
    font-size: 10px !important;
}

html.honeywell-dark-theme .hcharts .highcharts-button-box{
    fill: #45a7ea !important;
}